body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    position: relative;
}
#background{
    background: url('https://vkirirom.com/images/HomePage/slider/PineResort1.jpg') center center no-repeat;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

}
#background:before {
    content: '';
    background-image: -webkit-gradient(linear, left top, right bottom, from(gray), to(gray));
    background-image: -webkit-linear-gradient(top left, gray, gray);
    background-image: -o-linear-gradient(top left, gray, gray);
    background-image: linear-gradient(to bottom right, gray, gray);
    opacity: 0.7;
}

body::-webkit-scrollbar{
    display: none;
}
@media (max-height: 568px) {
    div[style $='transform: translate(0px, 64px);'] {
        -webkit-transform: translateY(0) !important;
            -ms-transform: translateY(0) !important;
                transform: translateY(0) !important;
    }
}




